@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: normal;
  src: url("fonts/ptsans_regular.woff") format("woff");
}
@font-face {
  font-family: "PT Sans Bold";
  font-style: normal;
  font-weight: normal;
  src: url("fonts/ptsans_bold.woff") format("woff");
}
@font-face {
  font-family: "Arvo";
  font-style: normal;
  font-weight: normal;
  src: url("fonts/arvo_regular.woff") format("woff");
}
@font-face {
  font-family: "Arvo Gruen";
  font-style: normal;
  font-weight: normal;
  src: url("fonts/arvo_gruen.woff") format("woff");
}
body {
  string-set: strPage attr(data-str-page);
}

@page {
  margin: 1.5cm 1.2cm 1.5cm 1.8cm;
  padding: 0.5cm 0 1.5cm 0;
  size: A4 portrait;
  border-top: solid 1px black;
  border-bottom: solid 1px black;
}
@page {
  @top-right {
    content: string(title);
    padding-top: 0.7cm;
    z-index: -1;
    font-family: "Arvo";
    font-size: 0.8em;
  }
  @bottom-right {
    content: string(strPage) " " counter(page);
    padding-bottom: 0.7cm;
    z-index: -1;
    font-family: "Arvo";
    font-size: 0.8em;
  }
}
@page header {
  border: none;
  margin-top: 0cm;
  margin-bottom: 0cm;
  @top-right {
    content: none;
  }
  @bottom-right {
    content: none;
  }
}
h1 {
  string-set: title content();
}

article > header:first-child {
  page: header;
}

body {
  font-family: "PT Sans";
  font-size: 15px;
}

.headerWhere {
  font-family: "Arvo";
  font-size: 14px;
  text-align: right;
  font-weight: normal;
  margin: 15px 0 0 0;
  overflow: auto;
}
.headerWhere .logo {
  float: left;
  text-align: left;
  max-width: 40%;
}
.headerWhere .logo img {
  max-width: 100%;
  max-height: 4cm;
}
.headerWhere .introSmall {
  font-size: 0.9em;
}

.headerMain {
  border: solid 0.7px black;
  padding: 0.3cm;
  margin-top: 0.5cm;
  margin-bottom: 1.5cm;
}
.headerMain h1 {
  font-size: 1em;
  margin: 0;
  font-weight: normal;
}
.headerMain .prefix {
  font-size: 1.3em;
  font-weight: bold;
}
.headerMain table {
  margin-top: 0.3cm;
}
.headerMain td {
  font-weight: normal;
  padding: 0;
  margin: 0;
  vertical-align: top;
}
.headerMain th {
  font-weight: normal;
  padding: 0 0.5cm 0 0;
  margin: 0;
  vertical-align: top;
}

aside {
  float: right;
  width: 5cm;
  font-size: 12px;
  margin-left: 1.5cm;
  margin-bottom: 0.7cm;
}
aside img {
  max-width: 100%;
  width: 100%;
  height: auto;
}
aside p {
  margin: 0.5em 0;
}
aside ul, aside ol {
  margin-top: 1em;
}
aside dl dt, aside dl dd {
  display: block;
  margin: 0;
  padding: 0;
}
aside dl dt {
  text-decoration: underline;
}
aside dl dd {
  margin-bottom: 10px;
}

main p {
  margin: 0 0 0.7em;
  line-height: 1.2;
}
main img {
  max-height: 150px;
  max-width: 60%;
}

h2 {
  font-family: "Arvo";
  font-size: 1.2em;
  margin-top: 1.7em;
}

